import React from 'react';
import useApiRequest from "../hooks/userApiRequest";
import {Button} from "@material-ui/core";
import DrinksList from "../common/DrinksList";
import ErrorHandler from "../common/ErrorHandler";

function Categories() {
  const [{status, payload}, makeRequest] = useApiRequest('https://www.thecocktaildb.com/api/json/v1/1/list.php?c=list')

  return (
    <div>
      <Button className="get-cocktail" onClick={makeRequest}>
        load categories
      </Button>
      {!payload && <p>nothing is here</p>}
      <ErrorHandler status={status}>
        { payload && <DrinksList drinks={payload.drinks} categories/>}
      </ErrorHandler>
      </div>
  );
}

export default Categories;