import _ from "lodash";
import React from "react";
import { withRouter } from 'react-router-dom';

function DrinksList({drinks, history}) {
  return <div className="cocktails-list">
    {_.map(drinks, cocktail => {
      const {strAlcoholic, strGlass, strCategory, strDrinkThumb, strDrink} = cocktail;

      return <div
        onClick={()=> history.push(`/cocktail/${cocktail.idDrink}`)}
        key={cocktail.idDrink + cocktail.strCategory}
        className="cocktail-wrapper"
      >
        {
          strDrinkThumb && <div className="cocktail-image">
            <img src={strDrinkThumb || 'https://thaibah.com/dev/assets/img/default-img.jpg'} alt=""/>
          </div>
        }
        <div className="cocktail-info">
          <div className="cocktail-name">
            {strDrink || ''}
          </div>
          <div className="cocktail-ingredients">
            {strAlcoholic && <span><b>Alco:</b> {strAlcoholic}</span>}
            {strGlass && <span><b>Glass:</b> {strGlass}</span>}
            {strCategory && <span><b>Category:</b> {strCategory}</span>}
          </div>
        </div>
      </div>
    })
    }
  </div>
}

export default withRouter(DrinksList);