import React, {useState, useEffect} from 'react';
import axios from 'axios';
import _ from 'lodash';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/core/styles';
import DrinksList from "../common/DrinksList";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <span>{children}</span>
        </Box>
      )}
    </div>
  );
}
TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function CocktailType() {
  const [alcoholicTypes, setAllAlcoholic] = useState([])
  const [loading, setLoading] = useState(false)
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue = 0) => {
    setLoading(true);
    setValue(newValue);

    const name = _.get(event, ['currentTarget', 'name'], '')
    axios.get(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?${name? `a=${name}`: `c=Ordinary_Drink`}`)
      .then(({data}) => {
        if (data.drinks) {
          setAllAlcoholic(data.drinks)
          setTimeout(()=>{setLoading(false)}, 1000);
        }
      })
      .catch(err => console.log(err))
  };

  useEffect(() => {handleChange()}, [])

  return (
    <div className={classes.root}>
      <AppBar position="static" className="tab-header">
        <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
          <Tab label="Ordinary" {...a11yProps(0)} />
          <Tab name="Alcoholic" label="Non Alcoholic" {...a11yProps(1)} />
          <Tab label="Non_Alcoholic" {...a11yProps(2)} />
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        {!loading && <DrinksList drinks={alcoholicTypes}/>}
      </TabPanel>
      <TabPanel value={value} index={1}>
        {!loading && <DrinksList drinks={alcoholicTypes}/>}
      </TabPanel>
      <TabPanel value={value} index={2}>
        {!loading && <DrinksList drinks={alcoholicTypes}/>}
      </TabPanel>

      {loading && <div className="loading"/>}
    </div>
  );
}

export default CocktailType;
