import React from 'react';
import { useFormik } from 'formik';
import { TextField, Button } from '@material-ui/core';
import * as Yup from "yup"
import {ContextApp} from "../../reducers";
import Success from "./Success";
import {LOGIN} from "../../actions/types";

const validationSchema = Yup.object({
  password: Yup.string().required("Password name is Required"),
  email: Yup.string().email().required("Email is Required"),
})

const LoginPage = () => {
  const {handleSubmit, values, errors, handleChange } = useFormik({
    initialValues: {
      password: '',
      email: '',
    },
    validationSchema,
    onSubmit: values => {},
  });

  return (
    <ContextApp.Consumer>
      {({state, dispatch}) => {
        const {isLogged, registerFirst} = state;
        return (
          <div className="login-page">
            {
              !isLogged &&
              <form onSubmit={handleSubmit} className="flex flex-column">
                {errors['email']}
                <TextField
                  className="text-field"
                  id="email"
                  name="email"
                  type="email"
                  variant='outlined'
                  label='Email'
                  onChange={handleChange}
                  value={values.email}
                />

                {errors['password']}
                <TextField
                  className="text-field"
                  id="password"
                  name="password"
                  type="password"
                  variant='outlined'
                  label='Password'
                  onChange={handleChange}
                  value={values.password}
                />
                <Button
                  onClick={()=>dispatch({type: LOGIN, payload: values})}
                  type="submit">Login</Button>
              </form>
            }
            {
              isLogged &&
             <Success/>
            }
            {registerFirst && "User doesn't exist, please sign in first"}
          </div>
        )
      }}
    </ContextApp.Consumer>
  );
};

export default LoginPage;