import React, {useEffect} from 'react';
import {NavLink as Link, withRouter} from 'react-router-dom';
import _ from 'lodash';
import useApiRequest from "../hooks/userApiRequest";
import ErrorHandler from "../common/ErrorHandler";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";

function CocktailInfoPage(props) {
  const getId = _.get(props, ['match', 'params', 'id'], '11007')
  const [{status, payload}, makeRequest] = useApiRequest(`https://www.thecocktaildb.com/api/json/v1/1/lookup.php?i=${getId}`)
  const ingredient = /strIngredient.+/

  useEffect(()=> {
    makeRequest()
  }, [])

  const getDrink = _.first(_.get(payload, 'drinks', []))
  const ingredients = _.map(getDrink, (drink, key) => {
    if(ingredient.test(key) && drink)  {
      return {
        ingredient: {
          name: drink,
          link: `https://www.thecocktaildb.com/images/ingredients/${_.toLower(drink)}.png`
        }}
    }
  }).filter((val)=>!!val)

  return (
    <div>
      <div>
        <Link className="back-button flex flex-align-start" to={'/cocktails'}>
          <ArrowBackIosIcon/>
          <span>Go back</span>
        </Link>
      </div>
      <br/>
      <ErrorHandler status={status}>
        {
          payload && <div className="info-section flex flex-column flex-center">
            {getDrink.strTags && <div className="tag">{getDrink.strTags}</div>}
            <div>
              <img src={getDrink.strDrinkThumb} alt=""/>
            </div>
            <div className="ingrediets">
              <h1>{getDrink.strDrink}</h1>
              <p>{getDrink.strInstructions}</p>
              <h3>{getDrink.strGlass}</h3>
              <div className="flex flex-center">
                {_.map(ingredients, ({ingredient: {name, link}}) => <div key={name} className="flex flex-center flex-wrap">
                  <img className="small-img" src={link} alt={name}/>
                  <span>{name}</span>
                </div>)}
              </div>
            </div>
          </div>
        }
      </ErrorHandler>
    </div>
  );
}

export default withRouter(CocktailInfoPage);
