import React from 'react';
import { Button } from '@material-ui/core';
import useApiRequest from "../hooks/userApiRequest";
import ErrorHandler from "../common/ErrorHandler";
import DrinksList from "../common/DrinksList";

function RandomPage() {
  const [{status, payload}, makeRequest] = useApiRequest('https://www.thecocktaildb.com/api/json/v1/1/random.php')

  return (
    <div>
      <div className="cocktails-list flex flex-column center">
        <Button onClick={makeRequest}> show Random </Button>
        <ErrorHandler status={status}>
          { payload && <DrinksList drinks={payload.drinks}/>}
        </ErrorHandler>
      </div>
    </div>
  );
}

export default RandomPage;
