import React from 'react'
import {ContextApp} from "./reducers";

function ProtectedRoute(props)  {
  const Component = props.component;

  return (
    <ContextApp.Consumer>
      {({state})=>{
         return (
          state.user.isAdmin ?
          <Component /> : <div style={{margin: '50px'}}>
            Sorry, you don't have rights to watch this page
            </div>
          )
      }}
    </ContextApp.Consumer>
  )
}

export default ProtectedRoute;