import React from 'react';
import { useFormik } from 'formik';
import { TextField, Button } from '@material-ui/core';
import * as Yup from "yup"
import {ContextApp} from "../../reducers";
import {SIGNIN} from "../../actions/types";
import Success from "./Success";

const validationSchema = Yup.object({
  firstName: Yup.string().min(8).required("First name is Required, min 8char"),
  lastName: Yup.string().required("Last name is Required"),
  email: Yup.string().email().required("Email is Required"),
  password: Yup.string().required("Password is Required"),
})

const RegisterPage = () => {
  const { handleSubmit, values, errors, handleChange } = useFormik({
    initialValues: {
      firstName: '',
      lastName: '',
      email: '',
    },
    validationSchema,
    onSubmit: values => {},
  });

  return (
    <ContextApp.Consumer>
      {
        ({state, dispatch}) => {
          const {successRegistered, userIsExist} = state;
          if(successRegistered) return <Success/>

          return (
          <div className="register-page">

            {
              userIsExist && <p>User is exist, please try another one or login</p>
            }

            <form onSubmit={handleSubmit} className="flex flex-column">
              {errors['firstName']}
              <TextField
                className="text-field"
                id="firstName"
                name="firstName"
                type="text"
                variant='outlined'
                label='First Name'
                onChange={handleChange}
                value={values.firstName}
              />

              {errors['lastName']}
              <TextField
                className="text-field"
                id="lastName"
                name="lastName"
                type="text"
                variant='outlined'
                label='Last Name'
                onChange={handleChange}
                value={values.lastName}
              />

              {errors['email']}
              <TextField
                className="text-field"
                id="email"
                name="email"
                type="email"
                variant='outlined'
                label='Email'
                onChange={handleChange}
                value={values.email}
              />

              {errors['password']}
              <TextField
                className="text-field"
                id="password"
                name="password"
                type="password"
                variant='outlined'
                label='Password'
                onChange={handleChange}
                value={values.password}
              />

              <Button onClick={() => dispatch({
                type: SIGNIN,
                payload: values
              })} type="submit">Sign In</Button>
            </form>
          </div>)
      }
      }
    </ContextApp.Consumer>
  );
};

export default RegisterPage;