import React from 'react';
import { NavLink as Link } from 'react-router-dom';
import LocalBarIcon from '@material-ui/icons/LocalBar';
import _ from 'lodash';

function SideBar() {
  const links = [
    {link: 'cocktails', name: 'Cocktails'},
    {link: 'categories', name: 'Categories'},
    {link: 'glasses', name: 'Glasses'},
    {link: 'ingredients', name: 'Ingredients'},
    {link: 'cocktail-type', name: 'Cocktail\'s type'},
  ]

  return (
    <div className="sidebar">
      <div className="top-buttons list">
        <Link to={'/'} className="flex">
          <LocalBarIcon/>
          <span>Local bar</span>
        </Link>
      </div>

     <div className='main-menu'>
       <div className="list">
         {_.map(links, ({name, link})=><Link key={name} to={`/${link}`}>{name}</Link>)}
         <hr/>
         <Link to={'/random'}>Random</Link>
         <Link to={'/test'}>Test memo and callback</Link>
       </div>
     </div>
    </div>
  );
}

export default SideBar;
