import React, {useState, useEffect} from 'react';
import axios from 'axios';
import _ from 'lodash';
import { NavLink as Link } from 'react-router-dom';

function Glasses() {
  const [glassesList, setAllGlasses] = useState([])

  useEffect(() => {
        axios.get('https://www.thecocktaildb.com/api/json/v1/1/list.php?g=list')
          .then(({data}) => {
            if (data.drinks) {
              setAllGlasses(data.drinks)
            }
          })
          .catch(err => console.log(err))
    }, [])

  return (
      <div className="cocktails-list flex">
        {_.map(glassesList, ({strGlass}) => {
          return <Link to={`/glasses/${strGlass}`}
                  key={strGlass} className="cocktail-wrapper flex flex-start">
            <div className="cocktail-name">
              {strGlass || 'no name'}
            </div>
          </Link>
        })}
      </div>
  );
}

export default Glasses;
