import React from "react";
import _ from 'lodash';
import {FETCHING, SUCCESS, ERROR, LOGIN, LOGOUT, SIGNIN} from '../actions/types';
export const ContextApp = React.createContext();

export const initialState = {
  status: null,
  response: null,
  users: [{
    firstName: 'Helga',
    email: 'some@some.com',
  }],
  user: {
    firstName: 'Helga',
    isAdmin: true
  },
  isLogged: true,
  userIsExist: false,
  registerFirst: false,
  successRegistered: false,
};

export const reducer = (state = initialState, { type, payload } = {}) => {
  switch (type) {
    case FETCHING:
      return {...initialState, status: FETCHING};

    case SUCCESS:
      return {...state, status: SUCCESS, payload};

    case ERROR:
      return {...state, status: ERROR, payload};

    case LOGIN: {
      const loginUser = payload;
      const exist = _.filter(state.users, user => user.email === loginUser.email);

      if (!_.isEmpty(exist)) {
        return _.assign({}, state, {user: _.first(exist), isLogged: true, registerFirst: false});
      }

      return _.assign({}, state, {user: {}, isLogged: false, registerFirst: true });
    }

    case LOGOUT: {
      return _.assign({}, state, {user: {}, isLogged: false});
    }

    case SIGNIN: {
      let newUser = payload;
      let newUsersArray = _.cloneDeep(state.users)
      const exist = _.some(state.users, user => user.email === payload.email)

      newUsersArray.push(newUser)
      if (exist) return _.assign({}, state, {userIsExist: true});
      return _.assign({}, state, {users: newUsersArray, successRegistered: true, userIsExist: false});
    }

    default:
      return state;
  }
};