import React, {useCallback, useEffect, useMemo, useState} from 'react';
import { Button } from '@material-ui/core';
import useApiRequest from "../hooks/userApiRequest";
import ErrorHandler from "../common/ErrorHandler";
import DrinksList from "../common/DrinksList";

const ChildComponent = ({action}) => {
  const [value, setValue] = useState(0);

  useEffect(()=> {
    let val = action();
    setValue(val);
  }, [action])

  return (
    <>
    Child: {value}
    </>
  )
}


function TestMemoCallback() {
 const [callbackCount, setCallbackCount] = useState(0)
 const [memoCount, setMemoCount] = useState(0)

  const memoFunction = () => {
   console.log(memoCount, "memo called")
  }

  const callbackFunction = useCallback(()=> {
    console.log(callbackCount, 'callback called');
    return callbackCount;
  }, [callbackCount])


  useMemo(memoFunction, [memoCount]);

  return (
    <>
      <ChildComponent action={callbackFunction}/>

      <button onClick={()=>setCallbackCount(callbackCount+1)}> Change callback </button>
      <button onClick={()=>setMemoCount(memoCount+1)}> Change memo </button>
    </>
  );
}

export default TestMemoCallback;
