import React from 'react';
import {Route, Switch} from 'react-router-dom';
import ProtectedRoute from './ProtectedRoute';
import Cocktails from './pages/Cocktails';
import Categories from './pages/Categories';
import Glasses from './pages/Glasses';
import Ingredients from './pages/Ingredients';
import CocktailType from './pages/CocktailType';
import LoginPage from './pages/Auth/LoginPage';
import RandomPage from './pages/RandomPage';
import RegisterPage from './pages/Auth/RegisterPage';
import MainPage from './pages/MainPage';
import CocktailInfoPage from './pages/CocktailInfoPage';
import IngredientsCocktails from './pages/IngredientsCocktails';
import GlassesCocktails from "./pages/GlassesCocktails";
import TestMemoCallback from "./pages/TestMemoCallback";

function Routes() {
  return (
    <Switch>
      <Route path="/" exact component={MainPage}/>
      <Route path="/cocktails" exact component={Cocktails}/>
      <Route path="/cocktail/:id" exact component={CocktailInfoPage}/>
      <Route path="/random" exact component={RandomPage}/>
      <Route path="/test" exact component={TestMemoCallback}/>
      <Route path="/login" exact component={LoginPage}/>
      <Route path="/register" exact component={RegisterPage}/>
      <ProtectedRoute path="/categories" exact component={Categories}/>
      <Route path="/categories" exact component={Categories}/>
      <Route path="/glasses" exact component={Glasses}/>
      <Route path="/glasses/:name" exact component={GlassesCocktails}/>
      <Route path="/ingredients" exact component={Ingredients}/>
      <Route path="/ingredients/:name" exact component={IngredientsCocktails}/>
      <Route path="/cocktail-type" exact component={CocktailType}/>
    </Switch>
  );
}

export default Routes;
