import React, {useEffect} from 'react';
import useApiRequest from "../hooks/userApiRequest";
import ErrorHandler from "../common/ErrorHandler";
import DrinksList from "../common/DrinksList";
import { withRouter } from 'react-router-dom';
import _ from "lodash";
import { NavLink as Link } from 'react-router-dom'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';

function GlassesCocktails(props) {
  const name = _.get(props, ['match', 'params', 'name'], 'Vodka');
  const [{status, payload}, makeRequest] = useApiRequest(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?g=${name}`)

  useEffect(()=> {makeRequest()}, [])

  return (
    <div>
      <div>
        <Link className="back-button flex flex-align-start" to={'/ingredients'}>
          <ArrowBackIosIcon/>
         <span>Go back</span>
        </Link>
      </div>

      <ErrorHandler status={status}>
        { payload && <DrinksList drinks={payload.drinks}/>}
      </ErrorHandler>
      </div>
  );
}

export default withRouter(GlassesCocktails);
