import React, {useState, useEffect} from 'react';
import axios from 'axios';
import _ from 'lodash';
import { NavLink as Link } from 'react-router-dom'

function Ingredients() {
  const [ingredientsList, setAllIngredients] = useState([])

  useEffect(() => {
        axios.get('https://www.thecocktaildb.com/api/json/v1/1/list.php?i=list')
          .then(({data}) => {
            if (data.drinks) {
              setAllIngredients(data.drinks)
            }
          })
          .catch(err => console.log(err))
    }, [])

  return (
      <div className="cocktails-list flex">
        {_.map(ingredientsList, ({strIngredient1}) => {
          console.log(strIngredient1,'cocktail')
          return <Link to={`/ingredients/${strIngredient1}`}
            key={strIngredient1}
            className="cocktail-wrapper flex flex-start">
            <div className="cocktail-name">
              {strIngredient1 || 'no name'}
            </div>
          </Link>
        })}
      </div>
  );
}

export default Ingredients;
