import {FETCHING, SUCCESS, ERROR, LOGIN, SIGNIN, LOGOUT} from './types';

export const fetching = () => ({ type: FETCHING });
export const success = payload => ({ type: SUCCESS, payload });
export const error = payload => ({ type: ERROR, payload });

export const logIn = payload => ({type: LOGIN, payload})
export const logOut = () => ({type: LOGOUT })
export const signIn = payload => ({type: SIGNIN, payload })