import React, {useState, useEffect} from 'react';
import {Button, InputBase} from "@material-ui/core";
import useApiRequest from "../hooks/userApiRequest";
import ErrorHandler from "../common/ErrorHandler";
import DrinksList from "../common/DrinksList";

function Cocktails() {
  const [searchValue, setSearchValue] = useState('');
  const [{status, payload}, makeRequest] = useApiRequest(`https://www.thecocktaildb.com/api/json/v1/1/search.php?f=${searchValue || 'a'}`)

  const onChange = ({target: {value = ''}}) => {
    setSearchValue(value)
  }

  useEffect(() => {
    makeRequest()
  }, [])

  return (
    <div className="cocktails-page">
      <div className="search">
        <span className="search-by">Search: {searchValue || 'A'}</span>
        <InputBase
          className="search-input"
          color='primary'
          value={searchValue}
          onChange={onChange}
          placeholder="Search by letter"
          inputProps={{'aria-label': 'search'}}
        />
        <Button onClick={makeRequest}>Search</Button>
      </div>
      <ErrorHandler status={status}>
        {payload && <DrinksList drinks={payload.drinks}/>}
      </ErrorHandler>
    </div>
  );
}

export default Cocktails;
