import React from 'react';
import {Button} from '@material-ui/core';
import {Link} from 'react-router-dom';
import Avatar from "@material-ui/core/Avatar";
import { ContextApp } from "../reducers/index";
import {LOGOUT} from "../actions/types";

function Header() {
  return (
    <ContextApp.Consumer>
      {({state, dispatch}) => {
        const { isLogged, user } = state;

        return (
          <div className="header">
            {
              isLogged &&
              <div className="flex flex-space-between">
                <div className="user flex flex-center">
                  <Avatar className="avatar" alt="Remy Sharp" src="https://www.tm-town.com/assets/default_female600x600-3702af30bd630e7b0fa62af75cd2e67c.png" />
                  <h3>{user.firstName}</h3>
                </div>
                <Button color='primary' onClick={()=>dispatch({type: LOGOUT})}>
                  Log out
                </Button>
              </div>
            }
            {
              !isLogged && <>
                <Link to={'/login'}>
                  <Button color='primary'>
                    Login
                  </Button>
                </Link>
                <Link to={'/register'}>
                  <Button color='primary'>
                    Sign In
                  </Button>
                </Link>
              </>
            }
          </div>
        )
      }}
    </ContextApp.Consumer>
  );
}

export default Header;
